import { Injectable } from '@angular/core';
import { Bee } from "@ethersphere/bee-js";

@Injectable({
  providedIn: 'root'
})
export class SwarmService {

  bee: any;

  constructor() { }

  initBee(url: string) {
    this.bee = new Bee(url);
  }

  async upload(postageBatchId: string, file: File) {
    return await this.bee.uploadFile(postageBatchId, file);
  }

  async download(cid: string) {
    return await this.bee.downloadFile(cid);
  }
}
